.btn {
  font-weight: var(--weight-b);
  color: var(--navy);
  font-size: var(--size-xs);
  background-color: var(--white);
  border: 2px solid var(--navy);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 186px;
  height: 60px;
  transition: all .3s;
  display: flex;
}

.btn:hover {
  background-color: var(--navy);
  color: var(--white);
}

.btn__span {
  color: var(--black);
  margin-left: 10px;
  transition: all .3s;
}

.btn:hover .btn__span {
  color: var(--white);
}

.btn.btn--white {
  cursor: pointer;
  color: var(--white);
  border: 2px solid var(--white);
  background-color: #0000;
  transition: all .3s;
}

.btn.btn--white:hover {
  background-color: var(--white);
  color: var(--black);
}

.btn__span--white {
  color: var(--white);
  margin-left: 10px;
  transition: all .3s;
}

.btn--white:hover .btn__span--white {
  color: var(--black);
}

@media (width <= 769px) {
  * {
    font-family: var(--neo);
    font-weight: var(--weight-r);
    font-size: var(--size-s);
  }

  html {
    font-size: 3.5px;
  }

  .btn {
    width: 135px;
    height: 45px;
  }

  .btn__span.material-symbols-outlined {
    font-size: 18px;
  }

  #wrap {
    width: 100%;
    margin: 0 auto;
    overflow-x: hidden;
  }

  header {
    z-index: 3;
    width: 100%;
    height: 60px;
    margin: 0 auto;
    position: absolute;
    left: 0;
    right: 0;
  }

  .Menu {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 60px;
    display: flex;
  }

  .logo {
    z-index: 0;
    width: 12%;
    margin-left: 2%;
    display: flex;
    position: relative;
  }

  .logo > p {
    align-self: center;
  }

  .menu-trigger {
    z-index: 15;
    width: 30px;
    height: 26px;
    margin-top: 15px;
    margin-right: 25px;
    position: relative;
  }

  .menu-trigger span {
    background-color: var(--white);
    border-radius: 4px;
    width: 100%;
    height: 4px;
    position: absolute;
    left: 10px;
  }

  .menu-trigger, .menu-trigger span {
    box-sizing: border-box;
    transition: all .4s;
    display: inline-block;
  }

  .menu-trigger span:first-of-type {
    top: 0;
  }

  .menu-trigger span:nth-of-type(2) {
    top: 11px;
  }

  .menu-trigger span:nth-of-type(3) {
    bottom: 0;
  }

  #menu_wrap {
    background-color: var(--white);
    z-index: 3;
    width: 100vw;
    height: 100vh;
    display: none;
    position: fixed;
  }

  .menu_index {
    flex-flow: wrap;
    justify-content: space-between;
    width: 80%;
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .menu_main {
    width: 100%;
  }

  .menu_main > ul > li {
    width: 100%;
    margin-bottom: 38px;
    position: relative;
  }

  .menu_main > ul > li > a {
    position: relative;
  }

  .menu_main > ul > li > a.fixed:after {
    content: "";
    background-color: var(--mint);
    width: 100%;
    height: 23px;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .menu_main > ul > li > a > span {
    z-index: 2;
    font-weight: var(--weight-t);
    font-size: 30px;
    position: relative;
  }

  .menu_info span {
    font-size: var(--size-xxs);
    color: var(--dark-gray);
  }

  .menu_info p {
    font-size: var(--size-xxs);
    color: var(--black);
    line-height: var(--line-t);
  }

  .sns span {
    font-size: var(--size-xxs);
    color: var(--dark-gray);
  }

  .sns p {
    font-size: var(--size-xs);
    color: var(--black);
  }

  .animate:after {
    content: "";
    background-color: var(--mint);
    width: 100%;
    height: 23px;
    animation: .3s linear emphasis;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  #slider {
    z-index: 1;
    background-color: #87ceeb;
    width: 100%;
    height: 600px;
    margin: 0 auto;
  }

  footer {
    background-color: #000;
    width: 100%;
  }

  #footer_content {
    flex-flow: wrap;
    justify-content: space-around;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
  }

  .footer_menu1 {
    width: 95%;
  }

  .footer_menu1 > ul {
    flex-direction: column;
    justify-content: center;
    display: flex;
  }

  .footer_menu1 > ul > li {
    margin-top: 40px;
    margin-left: 10px;
  }

  .footer_menu1 > ul > li > a {
    color: var(--dark-gray);
    font-size: var(--size-xxs);
  }

  .footer_menu2 {
    width: 95%;
  }

  .footer_menu2 > ul {
    flex-direction: column;
    justify-content: center;
    display: flex;
  }

  .footer_menu2 > ul > li {
    margin-top: 40px;
    margin-left: 10px;
  }

  .footer_menu2 > ul > li > a {
    color: var(--dark-gray);
    font-size: var(--size-xxs);
  }

  .footer_info {
    font-size: var(--size-xs);
    flex-direction: column;
    width: 95%;
    margin-left: 20px;
    display: flex;
  }

  .address {
    color: var(--dark-gray);
    margin-top: 40px;
  }

  .address > p, .phone > p, .email > p {
    font-size: var(--size-xxs);
    line-height: var(--line-s);
  }

  .address span {
    color: var(--white);
    font-size: var(--size-xxs);
  }

  .phone {
    color: var(--dark-gray);
    margin-top: 40px;
  }

  .phone span {
    color: var(--white);
    font-size: var(--size-xxs);
  }

  .email {
    color: var(--dark-gray);
    margin-top: 40px;
  }

  .email span {
    color: var(--white);
    font-size: var(--size-xxs);
  }

  .footer_search {
    font-size: var(--size-xs);
    width: 95%;
    margin-bottom: 30px;
    margin-left: 20px;
  }

  .footer_search p {
    color: var(--dark-gray);
    font-size: var(--size-xxs);
    margin-top: 40px;
  }

  .search {
    color: var(--white);
    border: none;
    border-bottom: 1px solid var(--white);
    font-size: var(--size-xxs);
    float: left;
    background-color: #000;
    margin-top: 8px;
    padding: 9px 20px;
  }

  .search:focus {
    color: var(--white);
    outline: none;
  }

  .footer_ch button {
    float: left;
    cursor: pointer;
    color: var(--white);
    background-color: #000;
    border: none;
    margin-top: 20px;
    padding: 6px;
    font-size: 13px;
  }

  .footer_solid {
    flex-direction: column;
    justify-content: center;
    width: 95%;
    margin-bottom: 30px;
    display: flex;
  }

  .footer_last {
    flex-flow: wrap;
    justify-content: space-between;
    width: 95%;
    margin-bottom: 50px;
    display: flex;
  }

  .last_text {
    color: var(--dark-gray);
    font-size: var(--size-xs);
    width: 100%;
    margin-bottom: 20px;
  }

  .last_text > p {
    font-size: var(--size-xxs);
  }

  .last_icon i {
    color: var(--white);
    font-size: var(--size-s);
    cursor: pointer;
    margin-left: 10px;
  }

  .menu-trigger.active span {
    background-color: var(--black);
  }

  .menu-trigger.active span:first-of-type {
    transform: translateY(11px)rotate(-45deg);
  }

  .menu-trigger.active span:nth-of-type(2) {
    opacity: 0;
    animation: .3s forwards active-menu-bar05;
    left: 50%;
  }

  @keyframes active-menu-bar05 {
    100% {
      height: 0;
    }
  }

  .menu-trigger.active span:nth-of-type(3) {
    transform: translateY(-11px)rotate(45deg);
  }

  @keyframes emphasis {
    0% {
      width: 0%;
    }

    100% {
      width: 100%;
    }
  }
}

